.App {
  text-align: center;
}

.card-white{
  border-color: black;
  border-style: solid;
  background-color: white;
  margin: 25px 8%;
  padding: 15px;
}
.card-detail div{
  font-size: 25px;
  padding: 5px 30px;
}
.card-detail{
  width: 100%;
}

.card-detail button{
  display: inline-block;
}
h3{
  text-align: center;
}
.right-detail{
  flex:3;
  text-align: center;
}
.imagePirate{
  height: 120px;
  padding: 1.5rem;
  flex: 1;
}
.image{
  width: 100%;
}
.img-list{
  height: 80px;
  width: 75px;
}
.container-body{
  background-color: #ff9900;
  padding: 15px;
  margin: 0px 5% 5% 5%;
  border-color: black;
  border-style: solid;
}
.container-form {
  display: inline;
}

.form {
  float:left; 
  width:50%;
}

.flex{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
label{
  font-weight: 700;
  padding-top: 15px;
}
.error{
  color: red;
}
.img{
  width: 90%;
  height: 250px;
}
.phrase{
  font-size: 50px;
  text-align: center;
  font-weight: 700px;
  margin-top: 50px;
}
.btn{
  border-style: solid;
  border-color:black;
  border: 2px solid black;
  font-weight: 600px;
}
.col-3{
  width: 40%;
}
.col-4{
  width: 60%;
}
.col-1{
  width: 10%;
}
.justify{
  text-align: center;
}
.flex-3{
  flex:3;
}

.flex-1{
  flex: 1;

}

.flex-2{
  flex: 2;

}

.header{
  padding: 15px;
  margin: 20px 5% 0 5%;
  background-color: #783f04;
  color: white;
  border-style: solid;
  border-color: black;
}
.flex-header{
  align-items: flex-end;
}

button>.btn {
  color: white;
  font-size: 10px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
